.top {
  position: sticky;
  padding: 10px;
  transition: 1s;
}
.down {
  opacity: 0;
  padding: 10px;
}
.up {
  padding: 10px;
  width: 100%;
  position: sticky;
  top: 0;
  animation-name: navOpacity;
  z-index: 1000;
  animation-duration: 1s;
  background-color: #0a192f;
}
.none {
  display: none;
}
.navbar-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbar-content li {
  color: #a7b2cf;
}
.navbar-content li a, .navbar-content li a:visited {
  color:#a7b2cf
}
.navbar-content button{
  
  cursor: pointer;
}
.navbar-content button a{
  color: #a7b2cf;
}
.m-icon {
  font-size: 36px;
  margin: 10px;
  /* background-color: transparent; */
  color: transparent;
  color: #5ef0cf;
  border: 8px solid #316e61;
  clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);
}
.img-logo-parent {
  background-color: transparent;
}
.navbar-elements{
  display: flex;
  align-items: center;
}
.navbar-elements button {
  color: #5ef0cf;
  border: 1px solid #5ef0cf;
  padding: 10px 15px;
  background-color: transparent;
  border-radius: 5px;
  margin-left: 20px;
  margin-right: 30px;
}
.navbar-elements button:hover {
  background-color: rgba(255, 255, 255, 0.068);
}
  .list-icon {
    display: none;
  }
  
  .m-icon {
    margin-left: 20px;
  }
  .navbar-container {
    padding: 10px 20px;
  }
  .intro-container {
    padding: 40px 80px;
  }
  .navbar-content{
    position: relative;
    
  }
  @media(min-width: 738px){
    .navbar-elements ol {
      display: flex;
      justify-content: center;
      align-items: center;
      color: #a7b2cf;
    }

  }
  .navbar-elements ol li {
    margin-left: 20px;
    margin-right: 15px;
    list-style: none;
    counter-increment: counter;
    font-size: 15px;
  }
  .navbar-elements ol li::before {
    content: "0" counter(counter) ".";
    color: #5ef0cf;
    margin-right: 5px;
  }
  .navbar-elements ol li:hover a {
    color: #5ef0cf;
    cursor: pointer;
  }

@media only screen and (max-width: 738px) {
  .navbar-elements ol{
    position: absolute;
    top: calc(100% + 20px);
    background-color: rgb(0, 0, 0);
    list-style: none;
    right: 10px;
    z-index: 1000;
    display: none;
  }
  ol::before{
    content: "";
    position: absolute;
    top: -20px;
    right: 0;
    border-style: solid;
    border-width: 10px;
    border-color: transparent transparent black transparent;
  }
  .navbar-elements ol li{
    padding: 10px;
    margin-left: 5px;
  }
  
  .navbar-elements ol li:not(:last-of-type){
    border-bottom: 1px solid #ddd;
  }
  .list-icon {
    display: block;
    color: #5ef0cf;
    font-size: 30px;
  }
}
@keyframes navOpacity {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
